html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--bg);
}
@media (min-width: 40.0625em) {
  body {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
  }
}

.container {
  max-width: 60rem;
  margin-inline: auto;
}
@media (min-width: 40.0625em) {
  .container {
    display: flex;
    background-color: white;
    padding-left: 16px;
    padding-top: 16px;
    border-radius: 0.9375rem;
    box-shadow: 0 25px 40px -20px hsla(0, 0%, 0%, 0.0951);
  }
}

.sidebar {
  min-width: 17.125rem;
}
@media (min-width: 40.0625em) {
  .sidebar {
    border-radius: 0.625rem;
    background: url("/assets/images/bg-sidebar-desktop.svg") no-repeat;
  }
}

.main {
  background-color: white;
  max-width: calc(100% - 2rem);
  margin-inline: auto;
  border-radius: 0.625rem;
  padding: 2rem 1.5rem;
  box-shadow: 0 25px 40px -20px hsla(0, 0%, 0%, 0.0951);
}
@media (min-width: 40.0625em) {
  .main {
    margin: 0;
    flex: 1;
    background-color: transparent;
    box-shadow: 0 25px 40px -20px hsla(0, 0%, 0%, 0);
    padding: 2rem 1.25rem;
  }
}

@media (min-width: 940px) {
  .main {
    padding-left: 100px;
    padding-right: 100px;
  }
}
h1, h2, h3 {
  margin-top: 0;
}

h2 {
  font-size: 1.5rem;
  font-size: clamp(1.5rem, 0.8rem + 3.02vw, 2rem);
  font-weight: 700;
  color: var(--accent);
}

a, a:visited, a:active {
  text-decoration: none;
}

body {
  font-family: "Ubuntu", sans-serif;
}

:root {
  --bg: #EFF5FF;
  --border:#D6D9E6;
  --accent: #022959;
  --text1: #9699AA;
  --text2: #ABBCFF;
  --unchecked: #D6D9E6;
  --arcade: #FFAF7E;
  --advanced: #F9818E;
  --pro: #483EFF;
  --error: #EE374A;
  --current-step: #BEE2FD;
}

.step {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.step__number {
  width: 2.0625rem;
  height: 2.0625rem;
  align-content: center;
  text-align: center;
  border: 1px solid white;
  border-radius: 50%;
  color: white;
  font-size: 0.875rem;
  font-weight: 700;
}
.step-active .step__number {
  color: var(--accent);
  background-color: var(--current-step);
  border: none;
}
.step__about span {
  font-size: 0.75rem;
  color: var(--text2);
  margin-bottom: 0.25rem;
}
.step__about p {
  font-size: 0.875rem;
  font-weight: 700;
  color: white;
  letter-spacing: 1px;
}
@media (max-width: 40em) {
  .step__about {
    display: none;
  }
}

.steps__container {
  display: flex;
  justify-content: center;
  min-width: 100%;
  max-width: 11.25rem;
  margin-inline: auto;
  gap: 1rem;
  padding: 2rem 0;
}
@media (min-width: 40.0625em) {
  .steps__container {
    padding: 2.5rem 2rem;
    margin: 0;
    flex-direction: column;
  }
}

.bg-mobile {
  z-index: -1;
  position: absolute;
  width: 100%;
}
@media (min-width: 40.0625em) {
  .bg-mobile {
    display: none;
  }
}

.main__header {
  margin-bottom: 1.375rem;
}
@media (min-width: 40.0625em) {
  .main__header {
    margin-bottom: 2.1875rem;
  }
}
.main__header h2 {
  margin-bottom: 9px;
}
@media (min-width: 40.0625em) {
  .main__header h2 {
    margin-bottom: 11px;
  }
}
.main__header p {
  line-height: 1.5625rem;
  color: var(--text1);
}

.input__field {
  display: flex;
  flex-direction: column;
  gap: 0.1875rem;
  color: var(--accent);
}
.input__field label {
  font-size: 0.75rem;
}
.input__field input {
  color: var(--accent);
  border: 1px solid var(--border);
  border-radius: 0.5rem;
  padding: 0.75rem 1rem;
}
@media (min-width: 40.0625em) {
  .input__field input {
    padding: 1rem;
  }
}
.input__field input::placeholder {
  color: var(--text1);
}
.input__field input, .input__field input::placeholder {
  font-size: 1rem;
  font-weight: 600;
}
@media (min-width: 40.0625em) {
  .input__field {
    gap: 0.5rem;
  }
  .input__field label {
    font-size: 0.875rem;
  }
}

#personalInfo {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
@media (min-width: 40.0625em) {
  #personalInfo {
    gap: 1.5rem;
  }
}

.form__controls {
  margin-top: 4.25rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.875rem;
  font-weight: 600;
}
@media (min-width: 40.0625em) {
  .form__controls {
    font-size: 1rem;
  }
}
@media (max-width: 40em) {
  .form__controls {
    width: calc(100vw - 32px);
    margin-inline: auto;
    position: absolute;
    bottom: 0;
    left: 16px;
    padding: 16px 0;
  }
}
.form__back {
  color: var(--text1);
  cursor: pointer;
}
.form__back:hover {
  color: var(--accent);
}
.form__submit {
  cursor: pointer;
  width: fit-content;
  border: none;
  border-radius: 0.25rem;
  background-color: var(--accent);
  color: white;
  font-size: 0.875rem;
  font-weight: 600;
  padding: 0.75rem 1rem;
}
@media (min-width: 40.0625em) {
  .form__submit {
    border-radius: 0.5rem;
    padding: 1rem 1.5rem;
  }
}